import {
    builder,
    Handler,
    HandlerContext,
    HandlerEvent,
} from "@netlify/functions";
import { configDotenv } from "dotenv";
import OpenAI from "openai";
import { Ratelimit } from "@upstash/ratelimit";
import { Redis } from "@upstash/redis";

configDotenv()

const getNplData: Handler = async (
    event: HandlerEvent,
    context: HandlerContext
) => {

    // console.log(event)




    // Static Data:
    const return_headers = {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Headers': '*',
        'Access-Control-Allow-Methods': 'GET',
        'Netlify-Vary': 'query',
    }
    function errorMessage(message: string) {
        return {
            statusCode: 500,
            body: JSON.stringify({ message: message }),
            headers: return_headers
        };
    }
    const system_message_2 = `
    Create a JSON containing an Instruction pair based on user input.
    The Instruction pair has a Title and an Explainatory.
    The Instruction pair should help the user in finding an HTML-Element on a webage.
    Summarize how to find the HTML-Element in the Title, based solely on position from user input.
    Describe how to find the HTML-Element in the Explainatory, based on entire user input.`


    // Rate Limiting
    const redis = new Redis({
        url: process.env.UPSTASH_REDIS_REST_URL,
        token: process.env.UPSTASH_REDIS_REST_TOKEN,
    })
    const ratelimit = new Ratelimit({
        redis: redis,
        limiter: Ratelimit.slidingWindow(50, "86400 s"),
        analytics: true,
        prefix: "@upstash/ratelimit",
    });
    const identifier = "api";
    const { success } = await ratelimit.limit(identifier);
    if (!success) {
        return errorMessage("You are effeced by Rate Limiting");
    }



    // Search Paramters:
    const _search_parms = new URLSearchParams(event.rawQuery)
    const _input_data_found = _search_parms.get("data")
    const user_id = _search_parms.get("user_id")
    if (!_input_data_found || !user_id) {
        return errorMessage("The data parameter is null")
    }
    const input_data = JSON.parse(_input_data_found)





    try {

        const openai = new OpenAI({
            organization: process.env.OPENAI_ORGANIZATION_ID,
            project: process.env.OPENAI_PROJECT_ID,
            apiKey: process.env.OPENAI_API_KEY,
        })

        const completion = await openai.chat.completions.create({
            model: "gpt-4o-mini",
            messages: [
                { role: "system", content: system_message_2 },
                { role: "user", content: JSON.stringify(input_data) },
            ],
            response_format: {
                // See /docs/guides/structured-outputs
                type: "json_schema",
                json_schema: {
                    name: "overred_schema",
                    description: "Always returns a Title and an Explainatory, based on the input",
                    schema: {
                        type: "object",
                        properties: {
                            title: {
                                description: "The Title with a maximum of 50 characters.",
                                type: ["string", "null"],
                            },
                            explain: {
                                description: "The Explainatory with a maximum of 200 characters.",
                                type: ["string", "null"],
                            }
                        },
                        additionalProperties: false,
                        required: ["title", "explain"]
                    }
                }
            }
        });

        return {
            statusCode: 200,
            body: JSON.stringify({
                "data": {
                    "usage": completion.usage,
                    "model": completion.model,
                    "response": completion.choices[0].message.content
                }
            }),
            headers: return_headers
        };

    } catch (e) {
        console.log(e);
        return errorMessage("An error occured : " + e)
    }

};

const handler = builder(getNplData);

export { handler };